.elements__photo {
  display: block;
  width: 100%;
  height: 282px;
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  object-fit: cover;
  object-position: center top;
  box-sizing: border-box;
  cursor: pointer;
  background-color: var(--dark-color);
  color: var(--light-color);
}
